  
* {
  box-sizing: border-box;
  margin: 0;
}

.img-result{
  border-radius: 5%;
  margin: 10px;
}
header{
  display: flex;
  justify-content: space-around;
  position: fixed;
  border-bottom: 1px lightgrey solid;
  height: 60px;
  width: 100%;
  background-color: white;
  top:0px;
  padding-top:25px;
  margin-bottom: 25px;
  z-index: 500;


}
header i{
  font-size: 1.5em;
  color:grey;
}
.pinterest{
  font-size: 2em;
  color: red;
}
header span{
  padding: 10px;
  font-weight: bold;
  display: inline;
  text-align: right;
}


.input-text{
  background-color: #efefef;
  border: 0;
  border-radius: 5px;
  color: #333;
  font-size: 16px;
  font-weight: 600;
  padding: 0 20px 0 22px;
  width: 100%;
}

.all-items{
  display: flex;
  justify-content: space-around;
}
.container-result{
  margin:0 auto;
  width: 90%;
  position: relative;
  padding-top: 80px;
}

